@mixin button {
	@include tran(250ms);

	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 36px;
	height: 38px;
	padding: 4px 16px;
	font-size: 12px;
	border-radius: 2px;
	cursor: pointer;
	user-select: none;
	background-image: none;
	border: 1px solid transparent;
	outline: 0 none;
	text-transform: inherit;
	font-weight: normal;

	&:focus,
	&:active,
	&:hover,
	&:visited {
		outline: 0 none;
	}
}
