// Do not put any CSS in here, use globals.scss

$color-dark: #6f787e;
$color-dark-darker: #394044;

$color-gray-lighter: #f8f8f8;
$color-gray: #f0f0f0;
$color-gray-darker: #ddd;
$color-gray-darker-2: #bbb;
$color-gray-darker-3: #bfc3c5;
$color-gray-darker-4: #b1b4b6;

$color-green: #19bd9b;
$color-green-darker: #107a64;
$color-green-lighter: #d4ece6;

$color-yellow: #f9c815;
$color-yellow-lighter: #feefbc;
$color-yellow-darker: #7d6100;
$color-note: #facc3d;

$color-orange: #f99b2b;
$color-orange-2: #ef7a13;

$color-blue-darker: #265372;
$color-blue-darker2: #2283c6;
$color-blue-play: #3195d8;
$color-blue: #3498db;
$color-blue-lighter: #7fbce5;
$color-blue-lighter2: #ccebf3;

$color-red: #b94a48;
$color-red-darker: #ff344f;
$color-red-darker-100: #ba0f25;
$color-pink: #ffabb6;

$body-font-family: 'Roboto', sans-serif;
$headline-font-family: 'Roboto', sans-serif;

$z-index-header: 101;
$z-index-menu: 102;
$z-index-overlay-popup: 103;
$z-index-visitor-sessions: 104;

// Ditch bootstrap-sass
$screen-xs-max: 767px;
$line-height-base: 1.428571429;
$screen-lg-min: 1200px;
$screen-sm-max: 991px;
$screen-xs-min: 480px;

$color-facebook: #3f70ac;
$color-twitter: #6ab2e1;

$base: 16px;

// From player

$color-text: $color-dark-darker;
$heading-text-color: $color-dark-darker;
$min-width: 1000px;
$color-error-text: $color-gray-darker-4;

// Redesign

$denim: #3e5b78;

$color-green-r: #2ec09a;
$color-green-hover-r: #27a684;

$color-blue-r: #4693e0;
$color-blue-lighter-r: #a9d4ff;
$color-blue-lighter-2-r: #7ab2e6;
$color-blue-darker-r: #4f769e;

$color-red-r: #ff344f;

@import '../style/mixins/input.scss';
@import '../style/mixins/buttons.scss';
@import '../style/mixins/global.scss';

@mixin container() {
	.container__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 38px;
		flex-wrap: wrap;

		> div {
			display: flex;
			align-items: center;
		}
	}

	.container__header-title {
		color: $denim;
		font-size: 14px;
		font-weight: bold;
	}

	.container__header-selector {
		margin-left: 32px;
		width: 200px;

		@media (max-width: 499px) {
			margin-left: 16px;
		}

		&--first {
			margin-left: 0;
		}
	}

	.container__header-icon-button {
		border-radius: 2px;
		background-color: #ffffff;
		border: solid 1px #eae8e8;
		margin-left: 2px;
		margin-right: 0;
	}

	.container__header-button {
		margin-left: 8px;
	}

	.container__content {
		margin-top: 16px;
	}
}

@mixin table {
	.table-item {
		height: 80px;
		border-radius: 2px;
		background-color: #ffffff;
		box-shadow: 0 2px 4px 0 rgba(30, 81, 135, 0.08);
		border: solid 1px #fafafa;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 32px;
		font-size: 14px;
		flex-wrap: wrap;
		color: $denim;

		&__view-name {
			display: flex;
			align-items: center;

			.table-item__name {
				margin-left: 32px;
			}
		}

		&__actions {
			display: flex;
			align-items: center;
		}

		&__action {
			&--button {
				text-transform: uppercase;
				font-weight: bold;
			}
		}
	}

	.table-empty {
		font-size: 14px;
		margin-top: 32px;
		color: $denim;
	}

	.table-item + .table-item {
		margin-top: 2px;
	}

	.table-item__action + .table-item__action {
		margin-left: 32px;
	}
}

@mixin searchSection() {
	.search-section {
		&__title {
			color: #2ec09a;
			font-size: 12px;
			padding: 0 8px;
			text-transform: uppercase;
			display: flex;
			align-items: center;
			font-weight: bold;
			letter-spacing: 1px;
			user-select: none;
		}

		&__icon {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 18px;
			margin-right: 8px;
		}

		&__upgrade {
			position: absolute;
			top: 23px;
			left: 6px;
			font-size: 9px;
			color: #2ec09a;
			border: 1px solid #2ec09a;
			border-radius: 10px;
			padding: 1px 4px;
			background-color: white;
		}

		&__list {
			margin-top: 8px;

			ul {
				list-style-type: none;
				margin: 0;
				padding: 0;
				padding-right: 32px;
				color: #6f787e;
			}

			li {
				padding: 6px 8px;
				cursor: pointer;
				font-size: 14px;
				user-select: none;
				position: relative;

				&:hover {
					border-radius: 2px;
					background-color: #ededed;
				}
			}

			li.active {
				&::after {
					content: '';
					display: inline-block;
					margin-left: 8px;
					box-sizing: content-box;
					width: 8px;
					height: 8px;
					border-radius: 50%;
					background-color: $denim;
					position: absolute;
					right: -16px;
					top: 10px;
				}
			}
		}
	}
}

@mixin searchRule() {
	.rule {
		display: flex;
		flex-direction: column;

		&__header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 46px;
			background-color: $denim;
			border-top-left-radius: 2px;
			border-top-right-radius: 2px;
			color: white;
		}

		&-title {
			font-size: 16px;
			font-weight: bold;
			padding: 16px;
		}

		&-subtitle {
			padding: 8px;
		}

		&-close {
			padding: 16px;
			cursor: pointer;
		}

		&__body {
			display: flex;
			flex-direction: column;
			padding: 4px;
			border: solid 1px #eeeeee;
			border-bottom-left-radius: 2px;
			border-bottom-right-radius: 2px;
			color: $denim;
			font-size: 14px;

			> div:not(.component-select-input-v2) {
				display: flex;
				align-items: center;

				> *:not(:first-child) {
					margin-left: 4px;
				}

				> * {
					flex: 1;
				}

				> input[type='text'],
				> input[type='number'] {
					flex: 2;
				}
			}

			> div:not(:first-child) {
				margin-top: 4px;
			}
		}
		&__row {
			display: flex;
			align-items: center;
		}
		.loading {
			filter: blur(2px);
			pointer-events: none;
		}
	}
}

@mixin shadow($shadow) {
	box-shadow: $shadow;
}

@mixin popup($width: 580px, $height: 280px) {
	@include shadow(000010px 00rgba (0, 0, 0, 0.2));

	position: fixed;
	top: 50%;
	z-index: $z-index-overlay-popup;
	left: 50%;
	width: $width;
	margin-left: -$width / 2;
	height: $height;
	margin-top: -$height;
	border-radius: 4px;
	border: 1px solid #dddddd;
}

@mixin warning() {
	margin: 16px 0;
	background-color: $color-yellow-lighter;
	text-align: center;
	padding: 16px;
	font-size: 14px;

	div {
		margin-bottom: 8px;
	}

	.fa {
		font-size: 150%;
	}

	.title {
		color: $color-dark;
	}

	.action {
		color: $color-dark;
		text-decoration: underline;
		cursor: pointer;

		a {
			color: $color-dark;

			&:hover {
				color: $color-dark-darker;
			}
		}
	}
}

@mixin ellipsis {
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

@mixin badge {
	padding-left: 6px;
	padding-right: 6px;
	padding-top: 2px;
	padding-bottom: 2px;
	color: white;
	border-radius: 4px;
	display: inline-block;
	font-weight: normal;
	font-size: 11px;
	text-transform: uppercase;
}

@mixin badge-lg {
	padding-left: 8px;
	padding-right: 8px;
	padding-top: 4px;
	padding-bottom: 4px;
	font-size: 13px;
}

@mixin badge-free {
	@include badge();

	background-color: $color-gray-darker-2;
}

@mixin badge-premium {
	@include badge();

	background-color: $color-blue-lighter;
}

@mixin badge-beta {
	@include badge();

	background-color: $color-orange;
}

@mixin badge-operator {
	@include badge();

	background-color: $color-gray-darker;
	margin-left: $base;
	margin-top: $base / 4;
}

@mixin badge-upsell {
	@include badge();

	background-color: $color-green;
	margin-left: $base / 2;
	text-shadow: 1px 0 1px rgba(150, 150, 150, 1);
}

@mixin socialButton($backgroundColor, $color: #fff) {
	color: $color;
	background-color: $backgroundColor;
	border: none;

	&:hover,
	&:focus {
		color: $color;
		background-color: darken($backgroundColor, 10%);
	}
}

@mixin btn-action-secondary {
	outline: 0 none;
	transition: all 200ms linear;
	background-color: $color-gray-darker;
	border-color: $color-gray-darker;
	color: $color-dark-darker;
	min-width: 70px;
	border-radius: 2px;
	font-size: 13px;

	&:hover,
	&:active,
	&:focus {
		outline: 0 none;
	}

	&:hover {
		color: $color-dark-darker;
		background-color: #d3d3d3;
	}
}

// From player

@mixin transition($transition) {
	transition: $transition;
}

@mixin event($type) {
	box-sizing: content-box;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	border: 2px solid #f9f9f9;

	@if $type == 'click' {
		background-color: #4693e0;
		color: #4693e0;
	}

	@if $type == 'url' {
		background-color: #2ec09a;
		color: #2ec09a;
	}

	@if $type == 'error' {
		background-color: #ff344f;
		color: #ff344f;
		width: 8px;
		height: 8px;
	}

	@if $type == 'text' {
		background-color: #b98948;
		color: #b98948;
	}

	@if $type == 'knob' {
		width: 16px;
		height: 16px;
		background-color: #2ec09a;
		border-color: #2ec09a;
	}

	@if $type == 'note' {
		background-color: transparent;
		border-color: #f9d006;
	}

	@if $type == 'custom' {
		background-color: #6f787e;
		border-color: #f9f9f9;
	}
}

.icon-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
}
